import React from 'react'

class Home extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      tableValue: [],
      chooseIndex: null,
      priorityQueueOpen: [{index: 300, f: 0, g: 0}],
    }
  }

  row = (index) => {
    let row = [];
    for (let i = 0; i < 20; i++) {
      row.push(<div 
        onClick={async e => {
          await this.setState((prev) => {
            prev.tableValue = []
            prev.tableValue[index + i] = -1
            return { tableValue: prev.tableValue, chooseIndex: index + i, priorityQueueOpen : [{index: 300, f: 0, g: 0}]}
          });
          this.pathfinding()
        }} 
        key={index + i} 
        style={{
          width: "30px", 
          height: "30px", 
          border: "1px solid #000000", 
          display: "inline-block", 
          marginLeft:"3px", 
          marginBottom:"3px",
          textAlign: "center",
          lineHeight: "30px",
          color:"white",
          fontWeight: "bolder",
          backgroundColor: (i + index) == 300 ? "red" : (this.state.tableValue[index + i] == -1 ? "green" : this.state.tableValue[index + i]? "blue" : "white"),
          verticalAlign: "top"}}>
          {this.state.tableValue[index + i]}
          </div>)
    }
    return row
  }
  
  table = () => {
    let table = [];
    for (let i = 0; i < 20; i++) {
      table.push(<div style={{marginLeft: "30px"}} key={"row" + i}> { this.row( 20*i ) } </div>)
    }
    return table
  }

  pathfinding =async () => {
    while (this.state.priorityQueueOpen.length !== 0) {
      let q, index;
      for(let i = 0; i < this.state.priorityQueueOpen.length; i++) {
        if(!q || this.state.priorityQueueOpen[i].f < q.f) {
          q = {}
          q = Object.assign(q, this.state.priorityQueueOpen[i]);
          index = i;
        }
      }
      console.log(q)
      this.state.priorityQueueOpen.splice(index, 1)
      let x = q.index % 20;
      let y = (q.index - (q.index % 20))/20;
      let successor = {}
      let child = []
      for(let i = x-1; i < x+2; i++) {
        for(let j = y-1; j < y+2; j ++) {
          if(i >= 0 && j >= 0 && i < 20 && j < 20) {
            successor.index = j*20 + i;
            if(successor.index == this.state.chooseIndex) return successor.index
            successor.g = q.g + 1;
            successor.h = Math.sqrt(Math.pow(i - this.state.chooseIndex % 20,2) + Math.pow(j - this.state.chooseIndex / 20,2))
            successor.f = successor.g + successor.h
            if(!this.state.tableValue[successor.index] || this.state.tableValue[successor.index] > successor.f) {
              child.push(successor)
            }
          } else continue
        }
      }
      
    }
    return -1
  }

  render() {
    return (
      <div>
        {
          this.table()
        }
      </div>
    )
  }
}

export default Home